import hFormAlert from '@/components/h-form-alert/h-form-alert.vue'
export default {
	components: {
		hFormAlert
	},
	data() {
		return {
			min: 0,
			max: 500,
			currentWordNumber:"",
			modal6: false,
		}
	},
	methods: {
		confirm(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了确定"
			})
		},
		cancel(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了取消"
			})
		},
		show6() {
			this.modal6 = true
		},
		hide6() {
			this.modal6 = false
		},
	  inputs(e) {
		console.log(e)  
		this.currentWordNumber = e.detail.value.length;
	    },
	  modify(e){
		uni.showToast({
			icon:"none",
			title:"点击提交"
		})
	  }
}
}
